import React, { Component } from 'react';
import './ZStackScroller.css';

// const props = {
//   names: Array.from({ length: 100 }, (e, i) => { return "explosion00-frame" + pad(i+1, 3) + ".png"; }),
//   names2: numstacks ? Array.from({ length: 100 }, (e, i) => { return "explosion01-nofire-frame" + pad(i+1, 3) + ".png"; }) : null,
//   animSpeed: 100,
//   initialSlice: 50
// };


export default class ZStackScroller extends Component {
  constructor(props) {
    super(props);
    this.state = {
      images: [],
      images2: [],
      has2: false,
      currentIndex: 0,
      isPlaying: false,
      animationTimer: null,
      imageContainer: null, // document.getElementById("imagecontainer"),
      currentFilter: ""
    };

    // preload images
    //this.preloadImages();

    this.incrementImageIndex = this.incrementImageIndex.bind(this);
    this.setImageIndex = this.setImageIndex.bind(this);
    this.playNext = this.playNext.bind(this);
    this.toggleAnimation = this.toggleAnimation.bind(this);
  }

  setImageIndex(event) {
    this.setState({currentIndex: Number(event.target.value)});
    return true;
  }

  incrementImageIndex(dn) {
    let i = this.state.currentIndex + dn;
    // cycle up or down:
    if (i >= this.props.names.length) {
      i -= this.props.names.length;
    } 
    if (i < 0) {
      i += this.props.names.length;
    }
    this.setState({currentIndex: i});
  }

  has2() {
    return (
      this.props.names2 && 
      this.props.names2.length && 
      this.props.names2.length === this.props.names.length
    );
  }

  preloadImages() {
    if (!this.props.names) {
      console.log("Error: nothing to show?");
      return;
    }
    const images = [];
    for (let i = 0; i < this.props.names.length; ++i) {
      const img = new Image();
      img.src = this.props.names[i];
      images.push(img);
    }
    const images2 = [];
    if (this.has2()) {
      for (let i = 0; i < this.props.names2.length; ++i) {
        const img = new Image();
        img.src = this.props.names2[i];
        images2.push(img);
      }
    }
    this.setState({images, images2});
  }
  
  playNext() {
    this.incrementImageIndex(1);
    this.animationTimer = setTimeout(this.playNext, this.props.animSpeed);
  }

  toggleAnimation() {
    if (this.state.isPlaying) {
      clearTimeout(this.animationTimer);
      this.animationTimer = null;
    }
    else {
      this.playNext();
    }
    this.setState({isPlaying: !this.state.isPlaying});
  }

  componentDidMount() {
    this.preloadImages();
    this.setState({currentIndex: this.props.initialSlice});
  }

  componentWillUnmount() {

  }

  componentDidUpdate(prevProps, prevState, snapshot) {

  }

  render() {
    // construct this based on this.state
    return (
      <div className="ZStackScroller" id="zstackplayer">
        <div className="ZStackScroller-imagecontainer" id="imagecontainer">
          <img className="ZStackScroller-img" alt="" src={this.props.names[this.state.currentIndex]}></img>
        </div>

        <button className="ZStackScroller-btn ZStackScroller-playbtn" id="play"
        onClick={() => {this.toggleAnimation();} }
        >
          <i className="fa fa-play"></i>
        </button>

        <button className="ZStackScroller-btn ZStackScroller-nextbtn" id="nextslide"
        onClick={() => {this.incrementImageIndex(1);} }
        >
          <i className="fa fa-step-forward"></i>
        </button>
        
        <input className="ZStackScroller-slider" id="rangeslider" orient="vertical" type="range" step="1" 
          value={this.state.currentIndex} 
          min="0" 
          max={this.props.names.length}
          onChange={this.setImageIndex}
        ></input>    
        
        <button className="ZStackScroller-btn ZStackScroller-prevbtn" id="prevslide"
        onClick={() => {this.incrementImageIndex(-1);} }
        >
          <i className="fa fa-step-backward"></i>
        </button>
      </div>
    );
  }
}